.header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100vh;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  background-image: url(../images/header-bg.png);

  &__bottom {
    @include contentPaddingInline;    
    padding-bottom: 40px;

    @include onTablet {
      padding-bottom: 64px;
    }
  }

  &__waves {
    height: 57px;
    width: 48px;
    margin-bottom: 16px;

    @include onTablet {
      height: 76px;
      width: 64px;
      margin-bottom: 40px;
    }

    background: url(../images/sound-waves.svg) no-repeat center / cover;
  }

  &__title {
    font-style: italic;
    font-weight: 800;
    font-size: 32px;
    line-height: 100%;
    letter-spacing: -1.5px;

    @include onTablet {
      font-size: 40px;
    }
  }
}